<template>
    <div class="booklists-detail">
        <!-- 头部 -->
        <div class="detail-head">
            <h1>{{data.title}}</h1>
            <ul>
                <li v-for="item in tagsArr" :key="item.className" :class="item.className">
                    {{item.tag}}
                </li>
            </ul>
            <p :class="{textOverflow4: !down}">{{data.desc}}<van-icon v-show="!down" class="down" @click="clickDown" name="arrow-down" /></p>
            <div class="author">
                <div>
                    <img :src="isMale(data.gender)" />
                </div>
                <div class="author-msg">
                    <span class="nickname">{{author.nickname}}</span>
                    <span class="type">{{author.type}}</span>
                    <span class="lv">LV.{{author.lv}}</span>
                    <p>创建于{{data.created}}</p>
                </div>
            </div>
        </div>
        <!-- 列表 -->
        <div class="detail-list">
            <ul class="clearfix">
                <li class="active">全部({{data.total}})</li>
            </ul>
            <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
                class="book-list"
            >
                <div
                    v-for="(item, index) in books"
                    :key="index"
                    class="book-list-item"
                    @click="toBookInfo(item.book._id)"
                >
                    <div class="top">  
                        <div><img :src=" item.book.cover | fixImg " /></div>
                        <div class="book-info">
                            <div>
                                <h2>{{item.book.title}}</h2>
                            </div>
                            <div class="book-author">
                                <span><van-icon name="manager" /> {{item.book.author}}</span>
                            </div>
                            <div class="word">
                                <span>{{item.book.majorCate}}</span>
                                <span>{{(item.book.wordCount/10000).toFixed(1)}}万字</span>
                            </div>
                        </div>   
                    </div>
                    <div class="bottom">
                        {{item.comment}}
                    </div>
                </div>
            </van-list>
        </div>
        <!-- 底部 -->
        <div class="detail-bottom">
            <div class="share" @click="shareBookList">
                <van-icon class="icon" name="share" /> 分享书单
            </div>
            <div @click="collectBookList">
                <van-icon class="icon" :name="!isCollect ? 'like-o' : 'like'" /> {{!isCollect ? data.collectorCount : (data.collectorCount+1)}}收藏
            </div>
        </div>
        <van-action-sheet v-model="show" :round=false class="vansheet">
            <ul>
                <li v-for="item in shareList" :key="item.iconName">
                    <svg class="icon" aria-hidden="true">
                        <use :xlink:href="'#icon-'+item.iconName+''"></use>
                    </svg>
                    <p>{{item.title}}</p>
                </li>
            </ul>
        </van-action-sheet>
    </div>
</template>

<script>
import { fetchBookListsDetail } from '@/api/booklists'
import male from '@/assets/avatar/male.png'
import female from '@/assets/avatar/female.png'
export default {
    name: 'BooklistsDetail',
    data() {
        return {
            data: '',
            tags: [],
            author: '',
            avatarMale: male,
            avatarFemale: female,
            down: false,
            books: [],
            loading: false,
            finished: false,
            isCollect: false,
            show: false,
            shareList: [
                {title: 'QQ', iconName: 'QQ'},
                {title: 'QQ空间', iconName: 'QQkongjian'},
                {title: '微信好友', iconName: 'wx'},
                {title: '朋友圈', iconName: 'pengyouquan'}
            ]
        }
    },
    computed: {
        tagsArr() {
            let newArr = []
            if(this.tags.find(obj => obj == 'MIND_BOOKSTORE')) {
                newArr.push({tag: '走心粮草库', className: 'bookstore'})
            }
            if(this.tags.find(obj => obj == 'COLLECT_COUNT_X000+')) {
                newArr.push({tag: '收藏1000+', className: 'collect1000'})
            }
            if(this.tags.find(obj => obj == 'EDITOR_RECOMMEND')) {
                newArr.push({tag: '编辑推荐', className: 'recommend'})
            }
            if(this.tags.find(obj => obj == 'AUTHENTIC_LISTOWNER')) {
                newArr.push({tag: '认证单主', className: 'authentic'})
            }
            return newArr
        }
    },
    methods: {
        isMale(gender) {
            if(gender === "male") {
                return male
            } else if(gender === "female") {
                return female
            }
        },
        clickDown() {
            this.down = true
        },
        onLoad() {
            // 异步更新数据
            setTimeout(() => {
                fetchBookListsDetail(this.$route.params.id).then(res => {
                    this.data = res.data.bookList
                    this.tags = res.data.bookList.tags
                    this.author = res.data.bookList.author
                    let temp = res.data.bookList.books
                    for (let i = 0; i < 10; i++) {
                        this.books.push(temp[i]);
                    }
                    // 加载状态结束
                    this.loading = false;
                    // 数据全部加载完成
                    if (this.books.length >= temp.length) {
                        this.finished = true;
                    }
                })
            }, 500)
        },
        toBookInfo(id) {
            this.$router.push({name: 'BookInfo', params: {id: id}})
        },
        collectBookList() {
            if(this.isCollect === false) {
                this.isCollect = true
            } else {
                this.isCollect = false
            }
            
        },
        shareBookList() {
            this.show = true
        }
    }
}
</script>

<style lang="scss" scoped>
.booklists-detail {
    background-color: #eee;
    position: relative;
    .detail-head {
        padding: 0 10px;
        padding-top: 10px;
        background-color: #fff;
        h1 {
            font-size: 16px;
        }
        ul {
            padding: 10px 0;
            display: flex;
            justify-content: start;
            li {
                border-radius: 3px;
                font-size: 8px;
                padding: 3px 5px;
                margin-right: 5px;
            }
            .bookstore {
                color: rgb(0,204,102);
                background-color: rgba(0,204,102,.3);
            }
            .collect1000 {
                color: rgb(0,0,153);
                background-color: rgba(0,0,153,.3);
            }
            .recommend {
                color: rgb(255,102,102);
                background-color: rgba(255,102,102,.3);
            }
            .authentic {
                color: rgb(204,153,102);
                background-color: rgba(204,153,102,.3);
            }
        }
        p {
            font-size: 14px;
            line-height: 20px;
            color: #666;
            position: relative;
            .down {
                position: absolute;
                top: 60px;
                left: 330px;
                background-color: rgba(255,255,255,0.9);
            }

        }
        .author {
            padding: 15px 0;
            display: flex;
            justify-content: start;
            color: #999;
            img {
                width: 30px;
                height: 30px;
                border-radius: 15px
            }
            .author-msg {
                margin-left: 10px;
                span {
                    display: inline-block;
                    margin-right: 10px;
                }
                .nickname {
                    font-size: 13px;
                }
                .lv {
                    color: red;
                }
                p {
                    font-size: 10px;
                    color: #ddd;
                    padding-top: 5px;
                }
            }
        }
    }
    .detail-list {
        margin-bottom: 50px;
        ul {
            margin: 5px 0;
            padding: 10px;
            background-color: #fff;
            li {
                padding: 10px 15px;
                font-size: 14px;
                float: left;
            }
            .active {
                color: 	#ee0a24;
                border-radius: 2px;
                background-color: rgba(255,204,204, 0.2);
                border: 1px solid #ee0a24;
            }
        }
        .book-list {
            .book-list-item {
                background-color: #fff;
                margin-bottom: 5px;
                padding: 10px;
                .top {
                    display: flex;
                    justify-content: start;
                    border-bottom: 1px solid #ebebf0;
                    img {
                        width: 54px;
                        height: 72px;
                        margin-right: 10px;
                        padding-bottom: 10px;
                    }
                    .book-info {
                        h2 {
                            font-size: 16px;
                            padding-top: 3px;
                        }
                        .book-author {
                            padding: 10px 0;
                            color: #999;
                        }
                        .word {
                            color: #999;
                            span {
                                margin-right: 10px;
                            }
                        }
                    }
                }
                .bottom {
                    padding-top: 10px;
                    line-height: 20px;
                }
            }
        }
    }
    .detail-bottom {
        position: fixed;
        bottom: 0;
        z-index: 2;
        display: flex;
        justify-content: space-between;
        border-top: 1px solid #ebebf0;
        width: 100%;
        height: 50px;
        line-height: 50px;
        background-color: #fff;
        div {
            font-size: 16px;
            text-align: center;
            width: 187px;
            color: #ee0a24;
            .icon {
                position: relative;
                top: 2px;
            }
        }
        .share {
            border-right: 1px solid #ebebf0;
        }
    }
    .vansheet {
        ul {
            display: flex;
            justify-content: space-around;
            li {
                width: 90px;
                text-align: center;
                padding: 15px 0;
                .icon {
                    width: 1em;
                    height: 1em;
                    vertical-align: -0.15em;
                    fill: currentColor;
                    overflow: hidden;
                    font-size: 30px;
                }
                p {
                    margin-top: 10px;
                }
            }
        }
    }
}
</style>