import request from '@/utils/request'

// 获取书单
export function fetchBookLists(query) {
    return request({
      url: '/booklists',
      method: 'get',
      params: query 
    })
}

// 获取书单详情
export function fetchBookListsDetail(id) {
    return request({
      url: `/booklists/${id}`,
      method: 'get'
    })
}